    .viewedContent .viewSlide .userView {width: 30.5556vw;}

    .swiper-pagination-bullet {width: 2.7778vw !important;height: 2.7778vw !important;}

    .width720_cont1 {margin: 8.3333vw 0; padding: 2.7778vw 0;}
    .width720_cont2 {margin: 13.8889vw 0 0;}
    .width720_aniTitle {font-size: 3.8889vw !important; margin-bottom: 4.4444vw !important; padding-left: 2.7778vw !important;}
    .width720_aniName {font-size: 2.6389vw !important; margin-top: 2.0833vw !important;}
    .viewedContent .viewSlide .userView .imgBox {height: 26.9444vw; border-radius: 0.5556vw;}
    .viewedContent2 .slideBox2 .userView .imgBox {height: 26.9444vw; border-radius: 0.5556vw;}

    .viewedContent .viewSlide {padding: 0 2.7778vw;}
    .viewedContent2 .slideBox2 {padding: 0 2.7778vw;}


    .swiper-button-prev:after, .swiper-rtl .swiper-button-next:after {font-size: 4.1667vw !important; }
    .swiper-button-next:after, .swiper-rtl .swiper-button-prev:after {font-size: 4.1667vw !important;}
    .swiper-button-prev, .swiper-button-next {top: 40% !important;}
    

    .viewedContent2 .byDay {font-size: 3.0556vw; padding-left: 2.7778vw; margin-bottom: 3.6111vw;}
    .viewedContent2 .byDay .day {max-width: 8.3333vw; height: 8.3333vw; margin-right: 2.7778vw;}

    .viewedContent.byTypePage {margin: 8.3333vw 0 8.3333vw; padding: 2.7778vw 0;     min-height: calc(100vh - 11.1111vw);}
    .viewedContent .viewSlide.component {grid-template-columns: repeat(3, 1fr); gap: 5.5556vw 1.3889vw;}
    .byTypeWrap {padding-top: 11.1111vw;}
    .byTypeWrap .byTypeInner {padding: 0 2.7778vw;}
    .byTypeWrap .byTypeInner .titleBox {padding: 5.5556vw 0; }
    .byTypeWrap .byTypeInner .titleBox h2 {font-size: 5.4444vw;}
    .byTypeWrap .byTypeInner .contentsBox .typeBox {display: block;}
    .byTypeWrap .byTypeInner .contentsBox .typeLise li {font-size: 3.5000vw; padding: 1.3889vw 2.7778vw; border-radius: 1.1111vw; margin-right: 1.6667vw; margin-bottom: 1.6667vw;}
